  // text 나타나기
  const greeting = document.querySelector('.greeting');
  const string = greeting.textContent;
  const split = string.split('');
  greeting.textContent = ""; 
  for(let i = 0; i <split.length; i++){
      greeting.innerHTML += '<span>'+split[i]+'</span>'
  } 
  let char = 0;
  let timer =setInterval(onTick, 80); 
  function onTick(){
      const span = greeting.querySelectorAll('span')[char];
      span.classList.add('fade');
      char++
      if(char === split.length){
          complete();
          show();
          return;
      }
  }
  function complete(){
      clearInterval(timer);
      timer = null;
  } 
  // text 나타난뒤 svg 나타나기
  function show(){
      const down_bar = document.querySelector('.down_bar');  
      down_bar.classList.add('show');
  } 
// parallax start
var ticking = false;  
var isFirefox = (/Firefox/i.test(navigator.userAgent));
var isIe = (/MSIE/i.test(navigator.userAgent)) || (/Trident.*rv\:11\./i.test(navigator.userAgent));
let scrollSensitivitySetting = 30; //Increase/decrease this number to change sensitivity to trackpad gestures (up = less sensitive; down = more sensitive) 
let slideDurationSetting = 800; //Amount of time for which slide is "locked"
let currentSlideNumber = 0;
let totalSlideNumber = $(".sections").length; 
let firstSlide = totalSlideNumber*0;
let lastSlide = totalSlideNumber-1;  
let nxt_btn = document.querySelector('.next_btn');
let prv_btn = document.querySelector('.prev_btn');
const git_btn = document.querySelector('.github');
function go_github(){
  location.href = "https://github.com/adele007fall/adele007fall.github.io";
}
const slide_down=function(){
  ticking = true;
  if (currentSlideNumber !== lastSlide) {
    currentSlideNumber++;
    nextItem(); 
    if (currentSlideNumber == lastSlide) { 
      nxt_btn.classList.add('un_btn');
    }
    if (currentSlideNumber !== firstSlide) {
      prv_btn.classList.remove('un_btn');
    }
  }
  slideDurationTimeout(slideDurationSetting); 
}
const slide_up=function(){
  ticking = true;
  if (currentSlideNumber == lastSlide) {
    nxt_btn.classList.remove('un_btn');
  } 
  if (currentSlideNumber !== 0) { 
    currentSlideNumber--; 
    if (currentSlideNumber == firstSlide) {
      prv_btn.classList.add('un_btn');
    }
    previousItem();
  }
  slideDurationTimeout(slideDurationSetting); 
}
nxt_btn.addEventListener('click',()=>{
  if (ticking != true) { 
   slide_down();
  } 
});
prv_btn.addEventListener('click',()=>{
  if(ticking != true){
    slide_up();
  }
})
function parallax(e) {
    if (isFirefox) {
        //Set delta for Firefox
        delta = e.detail * (-120);
      } else if (isIe) {
        //Set delta for IE
        delta = -e.deltaY;
      }else{
        //Set delta for all other browsers
        delta = e.wheelDelta;
      } 
    if (ticking != true) {
      if ( delta <= -scrollSensitivitySetting) {
        //Down scroll
        slide_down();
      } 
      if (delta >= scrollSensitivitySetting) {
        //Up scroll
        slide_up();
      } 
    } 
}


// ------------- SET TIMEOUT TO TEMPORARILY "LOCK" SLIDES ------------- //
function slideDurationTimeout(slideDuration) {
    setTimeout(function() {
      ticking = false;
    }, slideDuration);
  }
  
// ------------- ADD EVENT LISTENER ------------- //
var mousewheelEvent = isFirefox ? "DOMMouseScroll" : "wheel";
window.addEventListener(mousewheelEvent, _.throttle(parallax, 60), false);
 
// ------------- SLIDE MOTION ------------- //
function nextItem() {
    var $previousSlide = $(".sections").eq(currentSlideNumber - 1);
    $previousSlide.removeClass("up-scroll").addClass("down-scroll");
} 
function previousItem() {
    var $currentSlide = $(".sections").eq(currentSlideNumber);
    $currentSlide.removeClass("down-scroll").addClass("up-scroll");
}  
prv_btn.addEventListener('click',()=>{
    previousItem();
    console.log()
})
 